import { useState } from "react";
import "./contact.scss";
import {Call,LocationOn, Mail,Facebook,Twitter,LinkedIn} from "@material-ui/icons";

export default function Contact() {
  const [message, setMessage] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();
    setMessage(true);
  };
  return (
    <div className="contact" id="contact">
      <h1>Contact</h1>
     <div className="container">
     <div className="left">
       <h5>I love to listen and I am eagerly waiting to talk to you regarding the projects.
          Get in touch with me if you have any queries and I will get back to you as soon as possible.</h5>
      <form onSubmit={handleSubmit}>
        <input type="text" placeholder="Email" />
        <input type="text" placeholder="Name" />
        <textarea placeholder="Message"></textarea>
        <button type="submit">Send</button>
        {message && <span>Thanks, I'll reply ASAP :)</span>}
        </form>
      </div>
      <div className="right">
          <div className="itemContainer">
            <h4>Call Me</h4>
              <Call className="icon"/>
              <span>+94 711 942 777</span>
          </div>
          <div className="itemContainer">
            <h4>Mail Me</h4>
              <Mail className="icon"/>
              <span>sbamarasinghe97@gmail.com</span>
          </div>
          <div className="itemContainer">
              <h4>Visit Me</h4>
              <LocationOn className="icon"/>
              <span>45 Park Ave, Colombo, Sri Lanka</span>
          </div>
          <div className="itemContainer">
          <h4>Social Profiles</h4>
              <Facebook className="icon-social"/>
              <Twitter className="icon-social"/>
              <LinkedIn className="icon-social"/>
              
          </div>
      </div>
     </div>
     
    </div>
  );
}